import React from "react";
import { allArticles } from "../data";
import styled from "styled-components";

let Container = styled.div`
  border: solid;
  border-radius: 10px;
  border-width: 1px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 10px;
  margin-bottom: 20px;
  padding: 10px;
`;
let Title = styled.h2`
  font-family: "Bebas Neue", cursive;
  font-size: 34px;
  text-align: center;
  text-decoration: underline;
`;
let Image = styled.img`
  align-self: center;
  width: 200px;
`;
let Date = styled.h5`
  font-family: "Orbitron", sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin-right: 15px;
  text-align: end;
`;
let Text = styled.p`
  font-size: 20px;
  padding: 15px;
`;

const Why = () => {
  let articles = allArticles;
  let whyArticle;

  for (let i = 0; i < articles.length; i++) {
    if (articles[i].title == "Why?") {
      whyArticle = articles[i];
    }
  }

  return (
    <Container>
      <Title>{whyArticle.title}</Title>
      <Image src={whyArticle.image} />
      <Date>{whyArticle.date}</Date>
      <Text>{whyArticle.text}</Text>
    </Container>
  );
};

export default Why;
