import React from "react";
import { allArticles } from "../data";
import styled from "styled-components";

let Container = styled.div`
  border: solid;
  border-radius: 10px;
  border-width: 1px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 10px;
  margin-bottom: 20px;
  padding: 10px;
`;
let Title = styled.h2`
  font-family: "Bebas Neue", cursive;
  font-size: 34px;
  text-align: center;
  text-decoration: underline;
`;
let Image = styled.img`
  align-self: center;
  width: 200px;
`;
let Date = styled.h5`
  font-family: "Orbitron", sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin-right: 15px;
  text-align: end;
`;
let Text = styled.p`
  font-size: 20px;
  padding: 15px;
`;
const HomeArticle = () => {
  let articles = allArticles;

  let myVar = "hellooooooo";

  return (
    <Container>
      <Title>{articles[articles.length - 1].title}</Title>
      <Image src={articles[articles.length - 1].image} />
      <Date>{articles[articles.length - 1].date}</Date>
      <Text>${myVar}</Text>
    </Container>
  );
};

export default HomeArticle;
