import { Menu } from "@mui/icons-material";
import Nav from "react-bootstrap/Nav";
import { NavDropdown } from "react-bootstrap";
import Dropdown from "react-bootstrap/Dropdown";
import NavItem from "react-bootstrap/NavItem";
import NavLink from "react-bootstrap/NavLink";
import styled from "styled-components";
import { allArticles } from "../data";
import { renderSelected } from "..";

let Container = styled.div`
  /* border: solid; */
  /* border-bottom: solid;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px; */
  background-color: #eb3633;
  display: flex;
  justify-content: space-between;
  padding: 10px;
`;

let LogoContainer = styled.div`
  display: flex;
  gap: 10px;
  padding-top: 10px;
  padding-left: 10px;
`;

let Logo = styled.h2`
  color: white;
  font-family: "Fugaz One", cursive;
  font-size: 26px;
  font-weight: 500;
  letter-spacing: 2px;
  margin-left: 2px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
`;

let HomeLogoName = styled.h3`
  color: white;
  font-family: "Fugaz One", sans-serif;
  font-size: 26px;
  font-weight: 500;
  text-decoration: none;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
`;

let MyButton = styled.button`
  background-color: #eb3633;
  border: none;
  color: black;
  display: flex;
  margin-bottom: 2px;
  width: 100%;
`;

const Navbar = () => {
  let renderWhy = () => {
    document.getElementById("currentArticle").innerHTML = "";

    let articles = allArticles;

    for (let i = 0; i < articles.length; i++) {
      if (articles[i].title === "Why?") {
        let currentArticle = articles[i];
        renderSelected(currentArticle.id);
      }
    }
  };

  let renderLatest = () => {
    document.getElementById("currentArticle").innerHTML = "";

    let articles = allArticles;

    for (let i = 0; i < articles.length; i++) {
      if (i === articles.length - 1) {
        let currentArticle = articles[i];
        renderSelected(currentArticle.id);
      }
    }
  };

  let scrollToGrid = () => {
    let myEle = document.getElementById("currentArticle");

    window.scrollTo(0, myEle.scrollHeight);
  };

  return (
    <Container>
      <LogoContainer>
        <MyButton type="button" onClick={renderLatest}>
          <HomeLogoName>Jovany</HomeLogoName>
          <Logo>READS</Logo>
        </MyButton>
      </LogoContainer>
      <Nav>
        <Dropdown as={NavItem}>
          <Dropdown.Toggle as={NavLink} style={{ color: "black" }}>
            <Menu />
          </Dropdown.Toggle>
          <Dropdown.Menu
            style={{
              backgroundColor: "#EB3633",
              border: "solid",
              borderWidth: "1px",
              boxShadow: "5px 10px 18px black",
            }}
          >
            <MyButton type="button" onClick={renderLatest}>
              <Dropdown.Item
                style={{
                  backgroundColor: "white",
                  borderBottom: "solid",
                  borderRadius: "10px",
                  borderWidth: "2px",
                }}
              >
                Home
              </Dropdown.Item>
            </MyButton>
            <MyButton type="button" onClick={scrollToGrid}>
              <Dropdown.Item
                style={{
                  backgroundColor: "white",
                  borderBottom: "solid",
                  borderRadius: "10px",
                  borderWidth: "2px",
                  textAlign: "center",
                }}
              >
                All Blogs
              </Dropdown.Item>
            </MyButton>

            <NavDropdown.Divider />
            <MyButton type="button" onClick={renderWhy}>
              <Dropdown.Item
                id="whyButton"
                style={{
                  backgroundColor: "white",
                  borderBottom: "solid",
                  borderRadius: "10px",
                  borderWidth: "2px",
                }}
              >
                WHY?
              </Dropdown.Item>
            </MyButton>
          </Dropdown.Menu>
        </Dropdown>
      </Nav>
    </Container>
  );
};

export default Navbar;
